                        show ?
                            <span class="emotion-toggle-on" onClick={() => this.onClick(false)}>嘿嘿嘿 ヾ(≧∇≦*)ゝ</span>
                            :
                            <span class="emotion-toggle-off" onClick={() => this.onClick(true)}>戳我显示表情 OωO</span>
